img {
  width: 100%;
  height: auto;
}

body {
  overflow-x: hidden;
}

.searchBox {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 1rem;
}

.searchBox .text-field {
  margin: 0 1rem;
}

.searchBox .search-btn {
  margin: 0 2rem;
  background: #2c003e;
  color: white;
}

.searchBox .search-btn:hover {
  background: #512b58;
}

@media (max-width: 600px) {
  .searchBox .search-btn {
    margin-top: 1rem;
  }
}
